<template>
  <div id="find-notify">
    <navigation-bar  @onLeftClick="goBack" pageName="消息" />
        <ul class="notify-content" v-if="themeCurrent== '0'">
            <van-list
              v-model="loading"
              :finished="finished"
              @load="loadMoreList"
              finished-text="无更多数据..."
              :immediate-check="false"
            >
              <li v-for="(item,n) in messageList" :key="n" @click="nofityDetail(item)">
                <div class="user-avatar">
                  <img v-if="item.headPic" :src="item.headPic" alt />
                  <img v-else src="../../assets/images/find/photo.png" alt />
                </div>
                <div class="notify-con">
                  <p class="user-name">{{item.nickName}}</p>
                  <div class="activity">
                    <img v-if="!item.type" src="../../assets/images/find/praise_icon.png" />
                    {{item.evalMessage}}
                  </div>
                  <p class="time">{{item.createdTime}}</p>
                </div>
                <div class="note-con">
                  <img
                    v-if="item.notePic"
                    class="article-img"
                    :src="item.notePic"
                    v-lazy="item.notePic" :key="item.notePic"
                    alt
                  />
                    {{item.noteView}}
                </div>
                
              </li>
            </van-list>
        </ul>
        <ul class="notify-content-l" v-if="themeCurrent== '1'">
            <van-list
              v-model="loading"
              :finished="finished"
              @load="loadMoreList"
              finished-text="无更多数据..."
              :immediate-check="false"
            >
              <li v-for="(item,n) in messageList" :key="n" @click="nofityDetail(item)">
                <div class="user-avatar-l">
                  <img v-if="item.headPic" :src="item.headPic" alt />
                  <img v-else src="../../assets/images/find/photo.png" alt />
                </div>
                <div class="notify-con-l">
                  <p class="user-name-l">{{item.nickName}}</p>
                  <div class="activity-l">
                    <img v-if="!item.type" src="../../assets/images/find/praise_icon.png" />
                    {{item.evalMessage}}
                  </div>
                  <p class="time-l">{{item.createdTime}}</p>
                </div>
                <div class="note-con-l">
                  <img
                    v-if="item.notePic"
                    class="article-img-l"
                    :src="item.notePic"
                    v-lazy="item.notePic" :key="item.notePic"
                    alt
                  />
                    {{item.noteView}}
                </div>
                
              </li>
            </van-list>
        </ul>
  </div>
</template>

<script>
import { Toast } from "vant";
import { getMessageViewListService } from "../../services/findService";
import { store } from "../../utils/store";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "FindNotifyPage",
  activated() {
    this.themeCurrent = this.$curTheme;
    if (store.get("reloadFindNotifyList")) {
      store.dele("reloadFindNotifyList");
      this.page = 1;
      this.messageList = [];
      this.loading = false;
      this.finished = false;
      this.getMessageViewList();
    }
  },
  props: {},
  data() {
    return {
      messageList: [],
      finished: false,
      loading: false,
      requestType: 0, // 请求参数，0请求未读的，1请求全部的。
      page: 1,
      rows: 10,
      themeCurrent: 0,
    };
  },
  components: {
    NavigationBar
  },
  computed: {},
  watch: {},
  methods: {
    /**
     * 跳转到详情页面
     */
    nofityDetail(item) {
      if (item.noteStatus == 1) {
        this.$toast("帖子已删除");
      } else {
        this.$router.push({
          name: "findNotifyDetail",
          params: {
            noteCode: item.noteCode
          }
        });
      }
    },

    /**
     * 获取列表
     */
    getMessageViewList() {
      this.loading = true;
      getMessageViewListService({
        ownerCode: this.$userInfo.OWNER_CODE,
        areaCode: this.$userInfo.AREA_CODE,
        page: this.page,
        rows: this.rows,
        status: 0
      }).then(
        result => {
          this.loading = false;
          if (result.resultCode == "0") {
            this.finished = result.data.list.length < 10;
            if (this.page == 1) {
              this.messageList = result.data.list;
            } else {
              this.messageList = this.messageList.concat(result.data.list);
            }
          } else {
            this.$toast(result.msg);
            this.finished = true;
          }
          console.log(this.messageList);
        },
        error => {
          this.finished = true;
          this.loading = false;
          this.$toast(error);
        }
      );
    },

    loadMoreList() {
      this.page += 1;
      this.getMessageViewList();
    },

    /**
     * 返回上一页
     */
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
#find-notify {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  .notify-content {
    flex: 1;
    overflow: auto;
    padding: 0 15px;
    box-sizing: border-box;
    li {
      display: flex;
      padding: 13px 0;
      border-bottom: 0.5px solid #e5e5e5;
      .user-avatar {
        width: 38px;
        height: 38px;
        border-radius: 19px;
        img {
          @extend.user-avatar;
          object-fit: cover;
        }
      }
      .notify-con {
        flex: 1;
        margin-left: 12px;
        margin-right: 25px;
        > p {
          margin: 0;
        }
        .user-name {
          font-size: 14px;
          color: #333333;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 100%;
          line-height: 20px;
        }
        .activity {
          word-wrap: break-word;
          font-size: 14px;
          width: 220px;
          color: #333333;
          overflow: hidden;
          line-height: 23px;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          margin: 3px 0;
          img {
            width: 14px;
            height: 12px;
          }
        }

        .time {
          font-size: 10px;
          color: #9a9a9a;
        }
      }
      .note-con {
        word-wrap: break-word;
        width: 50px;
        height: 51px;
        font-size: 12px;
        line-height: 17px;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        color: #999999;
        > img.article-img {
          object-fit: cover;
          @extend .note-con;
        }
      }
    }
  }
  .notify-content-l {
    flex: 1;
    overflow: auto;
    padding: 0 15px;
    box-sizing: border-box;
    li {
      display: flex;
      padding: 13px 0;
      border-bottom: 0.5px solid #e5e5e5;
      .user-avatar-l {
        width: 38px;
        height: 38px;
        border-radius: 19px;
        img {
          @extend.user-avatar-l;
          object-fit: cover;
        }
      }
      .notify-con-l {
        flex: 1;
        margin-left: 12px;
        margin-right: 25px;
        > p {
          margin: 0;
        }
        .user-name-l {
          font-size: 18px;
          color: #333333;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 100%;
          line-height: 20px;
        }
        .activity-l {
          word-wrap: break-word;
          font-size: 18px;
          width: 220px;
          color: #333333;
          overflow: hidden;
          line-height: 23px;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          margin: 3px 0;
          img {
            width: 14px;
            height: 12px;
          }
        }

        .time-l {
          font-size: 16px;
          color: #9a9a9a;
        }
      }
      .note-con-l {
        word-wrap: break-word;
        width: 50px;
        height: 51px;
        font-size: 12px;
        line-height: 17px;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        color: #999999;
        > img.article-img {
          object-fit: cover;
          @extend .note-con;
        }
      }
    }
  }
}
/deep/.van-list__loading {
  display: none;
}
</style>
